<template>
	<div>
  <canvas class="verCanvas" width="100%" height="35px" @click="changeVeri"></canvas>
  <!--v-on  click  changeVeri  -->
  <h3></h3>
  </div>
</template>

<script>
/**
 *    
 * */
//	import axios from 'axios' 
  export default {
    name: 'login',
    data () {
      return {  // 
        canvas: "",
        width: "",
        height: "",
        ctx: ""
      }
    },
    mounted: function () { //   -- 
      this.canvas = document.getElementsByClassName('verCanvas')[0];
      this.width = this.canvas.width;
      this.height = this.canvas.height;
      this.ctx = this.canvas.getContext('2d');
      this.ctx.textBaseline = 'bottom';
//      this.drawPic("ddda") // 
      this.getVeri(this.drawPic);// 
    },
    methods: { // 
     getVeri: function (fn) {//   	
     this.$axios({   
        	methods:'get',
        	url:"/VueHandler/AdminLoginAndRegHandler?action=verification",
        }).then(function(response){ //200
        	if(response.data.success){ //
        		console.log(response)
        		fn(response.data.data)
        	}else{
        		alert('')
        	}
        }).catch(function(err){  // 404
        	alert(err)
        })
      },
      
      changeVeri:function () {// 
        //     
        this.getVeri(this.drawPic);
      },
      randomNum: function (min, max) {//
        return Math.floor(Math.random() * (max - min) + min);
      },
      randomColor: function (min, max) {// rgb
        var r = this.randomNum(min, max);
        var g = this.randomNum(min, max);
        var b = this.randomNum(min, max);
        return "rgb(" + r + "," + g + "," + b + ")";
      },
      drawPic: function (returnTxt) { // canvas
        /****/
        /****/
        this.ctx.fillStyle = this.randomColor(180, 240); //
        this.ctx.fillRect(0, 0, this.width, this.height);
        /****/
        for (var i = 0; i < 4; i++) {
          var txt = returnTxt[i];
          this.ctx.fillStyle = this.randomColor(50, 160);  //
          this.ctx.font = this.randomNum(25, 30) + 'px SimHei'; //
          var x = 10 + i * 20;
          var y = this.randomNum(25, 45);
          var deg = this.randomNum(-15, 15);
          //
          this.ctx.translate(x, y);
          this.ctx.rotate(deg * Math.PI / 180);
          // 
          this.ctx.fillText(txt, 10, 0);
          //
          this.ctx.rotate(-deg * Math.PI / 180);
          this.ctx.translate(-x, -y);
        }
        /****/
        for (var i = 0; i < 8; i++) {
          this.ctx.strokeStyle = this.randomColor(40, 180);
          this.ctx.beginPath();
          this.ctx.moveTo(this.randomNum(0, this.width), this.randomNum(0, this.height));
          this.ctx.lineTo(this.randomNum(0, this.width), this.randomNum(0, this.height));
          this.ctx.stroke();
        }
        /****/
        for (var i = 0; i < 100; i++) {
          this.ctx.fillStyle = this.randomColor(0, 255);
          this.ctx.beginPath();
          this.ctx.arc(this.randomNum(0, this.width), this.randomNum(0, this.height), 1, 0, 2 * Math.PI);
          this.ctx.fill();
        }
        return;
      }
    },
  };


</script>

<style>
  .verCanvas {
    margin-left: 5px;
    position: relative !important;

  }

  .verCanvas:hover {
    cursor: pointer;
  }

</style>



